body {
  margin: 0;
  padding: 0;
  /* font-family: 'Montserrat', sans-serif; */
  font-family: 'Nunito Sans', sans-serif;
}

* {
  margin: 0;
  box-sizing: border-box;
}

header {
  border-bottom: 1px rgba(38, 50, 56, 0.24) solid;
  padding-bottom: 8px;
  margin-bottom: 28px;
}

footer {
  font-size: 10px;
  text-align: center;
}
.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }
  .App-header:hover {
    color: red; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Nunito Sans', sans-serif;
  padding-top: 10px; }
  .header__title {
    font-size: 18px;
    font-weight: 700;
    color: #263238; }
  .header__cart {
    min-width: 24px;
    position: relative; }
    .header__cart:hover {
      cursor: pointer; }
  .header__num {
    position: absolute;
    font-size: 8px;
    top: 0;
    right: -8px;
    font-weight: 700;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    background-color: #F44336;
    color: #FFFFFF;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transition: all 0.3s;
    transition: all 0.3s; }

.icon-bar {
  width: 22px;
  height: 2px;
  display: block;
  -webkit-transition: all .2s;
  transition: all .2s;
  margin-top: 4px;
  background-color: #78909C; }

.cart__title {
  color: #78909C;
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 8px; }

.cart__summary {
  padding: 8px;
  background-color: #F7F9F9;
  margin-bottom: 16px; }

.cart__flex {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
  align-items: center; }

.cart__p {
  color: #78909C;
  font-size: 12px;
  font-weight: 600; }

.cart__total {
  font-size: 12px;
  font-weight: 700;
  color: #263238; }

.cart__btn--back {
  font-size: 14px;
  font-weight: 700;
  color: #2196F3; }
  .cart__btn--back:hover {
    cursor: pointer; }

.cart__btn--checkout {
  color: #FFFFFF;
  font-size: 14px;
  font-weight: 700;
  background: -webkit-linear-gradient(279.2deg, #00BCD4 0%, #00ACC1 99.05%);
  background: linear-gradient(170.8deg, #00BCD4 0%, #00ACC1 99.05%);
  border-radius: 20px;
  padding: 8px 24px; }
  .cart__btn--checkout:hover {
    cursor: pointer; }

.cart-item {
  padding-bottom: 8px;
  margin-bottom: 8px;
  border-bottom: 1px #b9dcee solid;
  width: 100%;
  -webkit-transition: all 1s;
  transition: all 1s; }
  .cart-item__name {
    text-transform: uppercase;
    color: #263238;
    font-weight: 600;
    font-size: 14px;
    margin-bottom: 4px; }
  .cart-item__brand {
    text-transform: uppercase;
    font-size: 14px;
    color: #78909C;
    margin-bottom: 8px; }
  .cart-item__flex {
    display: flex;
    justify-content: space-between; }
    .cart-item__flex--top {
      align-items: flex-start; }
    .cart-item__flex--center {
      align-items: center; }
  .cart-item__price {
    font-weight: 700;
    color: #263238;
    font-size: 16px; }
  .cart-item__btn {
    color: #2196F3;
    font-size: 12px;
    font-weight: 700; }
    .cart-item__btn:hover {
      cursor: pointer; }
  .cart-item__description {
    width: 100%;
    padding-left: 16px; }
  .cart-item__available {
    font-size: 10px;
    color: #78909C;
    font-weight: 700; }
  .cart-item__img {
    width: 56px;
    height: 56px; }

.quantity {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .quantity__active {
    color: #2C393F;
    width: 20px;
    background-color: #EFEFEF;
    text-align: center;
    font-weight: 900; }
    .quantity__active:hover {
      cursor: pointer; }
  .quantity__notactive {
    font-weight: 900;
    color: #78909C; }
    .quantity__notactive:hover {
      cursor: not-allowed; }
  .quantity__number {
    font-weight: 700;
    color: #2C393F;
    font-size: 12px;
    margin-left: 10px;
    margin-right: 10px; }

.main-box {
  position: relative;
  z-index: 5; }

.category__box {
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: rgba(219, 209, 209, 0.95);
  padding: 8px 16px;
  -webkit-transition: all 1s;
  transition: all 1s; }
  .category__box--hide {
    display: none; }
  .category__box--show {
    display: block; }

.category__close {
  position: absolute;
  right: 0;
  top: 0;
  padding: 2px;
  width: 20px;
  height: 20px;
  font-weight: 900;
  text-align: center; }

.filters {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px; }
  .filters__btn {
    color: #78909C;
    font-weight: 700;
    font-size: 14px;
    margin-left: 20px; }
    .filters__btn:hover {
      cursor: pointer; }

.sort-select {
  margin-left: 8px;
  outline: none;
  border-radius: 5px;
  font-size: 10px; }

.item {
  display: flex;
  justify-content: space-between;
  padding-bottom: 8px;
  margin-bottom: 8px;
  border-bottom: 1px #b9dcee solid;
  width: 315px;
  -webkit-transition: all 1s;
  transition: all 1s; }
  .item__name {
    text-transform: uppercase;
    color: #263238;
    font-weight: 600;
    font-size: 14px;
    margin-bottom: 4px; }
  .item__brand {
    text-transform: uppercase;
    font-size: 14px;
    color: #78909C;
    margin-bottom: 8px; }
  .item__flex {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .item__price {
    font-weight: 700;
    color: #263238;
    font-size: 16px; }
  .item__btn {
    color: #2196F3;
    font-size: 12px;
    font-weight: 700; }
    .item__btn:hover {
      cursor: pointer; }
  .item__description {
    width: 100%;
    padding-left: 16px; }
  .item__box {
    height: 112px; }
  .item__available {
    font-size: 10px;
    color: #78909C;
    font-weight: 700; }
  .item__img {
    width: 112px;
    height: 112px; }

